@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap);
* {
  box-sizing: border-box;
  font-family: "Lato", "Calibri", sans-serif;
}

body {
  background: #f6f8fb;
  margin: 0;
  padding: 0;
}

.App{max-width:1440px;margin:0 auto;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}ul{list-style:none;margin:0;padding:0}svg{cursor:pointer !important}h2{font-weight:700;font-size:40px;line-height:46px}
.Sidebar{width:20%;height:100vh;background-color:#fff;box-shadow:4px 0px 16px rgba(16,30,115,.08);position:-webkit-sticky;position:sticky;top:0}.Sidebar .logo{padding:24px}.Sidebar .logo img:last-child{display:none}.Sidebar p{font-weight:400;font-size:14px;line-height:20px;color:#a0a4a8;padding:14px 24px}.Sidebar li{padding:14px 5px}.Sidebar li:hover svg path{fill:#558eff}.Sidebar li img{object-fit:contain}.Sidebar li a{text-decoration:none;font-weight:700;font-size:14px;line-height:20px;color:#52575c;margin-left:20px}.Sidebar li a:hover{color:#558eff}.Sidebar li a span{margin:10px}.Sidebar li:last-child{border-top:1px solid #e8e8e8;margin-top:22px;padding-top:22px}
.search-and-notification{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;width:100%;-webkit-align-items:center;align-items:center;margin-bottom:15px}.search-and-notification .hamburger-menu{height:50px;width:50px;display:none;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;z-index:11111}.search-and-notification .hamburger-menu .hamburger-line{width:30px;margin:3px;height:2px;background-color:#000;border-radius:30px}.search-and-notification .search{width:100%;border-radius:100px;background-color:#fff;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:13px 16px}.search-and-notification .search input{border:none;padding-left:12px;width:100%}.search-and-notification .notification-profile{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;width:12%}.search-and-notification .notification-profile .notification{z-index:1;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;position:relative;cursor:pointer}.search-and-notification .notification-profile .notification .number{border:3px solid #fff;border-radius:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;position:absolute;left:18px;top:-3px}.search-and-notification .notification-profile .notification .number span{border-radius:100%;background-color:#ff6760;width:20px;height:20px;display:inline-block;text-align:center;color:#fff}.search-and-notification .notification-profile .profile{width:40px;height:40px;background-image:url(data:image/png;base64,);background-repeat:no-repeat;background-position:center;cursor:pointer}
.main{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:100%;padding:20px}
.cards{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin:15px 0}.cards .card-lists{display:-webkit-flex;display:flex;background-color:#fff;padding-left:24px;-webkit-align-items:center;align-items:center;width:22%;box-shadow:0px 12px 26px rgba(16,30,115,.06);border-radius:8px}.cards .card-lists .info-and-number{margin-left:24px}.cards .card-lists .info-and-number p:first-child{font-size:14px;font-weight:700;line-height:20px;text-align:left}.cards .card-lists .info-and-number p:last-child{font-size:18px;font-weight:400;line-height:24px;text-align:left;color:#336cfb}
.big-chart{box-shadow:0px 12px 26px rgba(16,30,115,.06);border-radius:8px;margin:15px 0;background-color:#fff}.big-chart .apexcharts-legend.apexcharts-align-right .apexcharts-legend-series,.big-chart .apexcharts-legend.apexcharts-align-left .apexcharts-legend-serie{display:-webkit-flex !important;display:flex !important}.big-chart .arrow_box{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;position:relative;background:#fff;border:1px solid #fff;box-shadow:0px 0px 8px rgba(16,30,115,.12);width:64px;height:32px}
.little-charts-container{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin:15px 0}.little-charts-container .little-chart{box-shadow:0px 12px 26px rgba(16,30,115,.06);border-radius:8px;background-color:#fff;margin:15px 0;width:49%}.apexcharts-xaxis-label{font-size:10px;font-weight:700;text-align:center;line-height:12px}
.table{background-color:#fff;box-shadow:0px 12px 26px rgba(16,30,115,.06);border-radius:8px}.table p{font-size:20px;font-weight:700;line-height:26px;margin:24px}.table table{width:100%;text-align:left;border-spacing:0}.table table thead tr{background-color:rgba(232,232,232,.2)}.table table thead tr th{font-size:14px;font-weight:700;line-height:20px;padding:20px;opacity:1}.table table tbody tr td{padding:15px;font-size:14px;font-weight:400;line-height:20px;border-top:1px solid #e8e8e8}.table table tbody tr td:last-child .image-name img{cursor:pointer}.table table tbody tr td .image-name{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center}.table table tbody tr td .image-name img{margin-right:12px}
.appointment{width:100%;padding:25px}.appointment .table-wrapper .table table tbody tr:last-child td{border-bottom:1px solid #dbdde0}.appointment .table-wrapper .table-buttons{padding:24px}.appointment .table-wrapper .table-buttons button{background:none;outline:none;border:1px solid #e8e8e8;border-radius:4px;width:32px;height:32px;margin:4px;cursor:pointer}.appointment .table-wrapper .table-buttons button:disabled{opacity:.7}.appointment .table-wrapper .table-buttons button:active,.appointment .table-wrapper .table-buttons button:focus{background:#336cfb;color:#fff}.appointment .icon img{position:fixed;right:0;bottom:0;margin:24px}
@media only screen and (max-width: 450px){body .main .cards{grid-template-columns:1fr;grid-template-rows:1fr 1fr 1fr 1fr}body .main .cards .card-lists{width:100%}body .main .search-and-notification{background:#fff;box-shadow:0px 4px 16px rgba(16,30,115,.08)}body .main .search-and-notification .notification-profile{width:35%}body .main .search-and-notification .hamburger-menu{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-left:16px}body .main .search-and-notification .hamburger-rotate .hamburger-line:first-child{transition:.5s;-webkit-transform:rotate(-45deg) translate(-2px, 2px);transform:rotate(-45deg) translate(-2px, 2px)}body .main .search-and-notification .hamburger-rotate .hamburger-line:nth-child(2){opacity:0}body .main .search-and-notification .hamburger-rotate .hamburger-line:last-child{transition:.5s;-webkit-transform:rotate(45deg) translate(-8px, -10px);transform:rotate(45deg) translate(-8px, -10px)}body .main .search-and-notification .search{display:none}body .Sidebar.active{left:0;width:100%;height:65vh;z-index:1111}body .Sidebar{transition:1s;position:fixed;top:0;left:-100%}body .Sidebar .logo img:first-child{display:block}body .Sidebar .logo img:last-child{display:none}body .Sidebar li{padding:5px}body .Sidebar li a{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center}body .Sidebar li a span{display:block}body .appointment .search-and-notification{background:#fff;box-shadow:0px 4px 16px rgba(16,30,115,.08)}body .appointment .search-and-notification .notification-profile{width:35%}body .appointment .search-and-notification .hamburger-menu{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-left:16px}body .appointment .search-and-notification .hamburger-rotate .hamburger-line:first-child{transition:.5s;-webkit-transform:rotate(-45deg) translate(-2px, 2px);transform:rotate(-45deg) translate(-2px, 2px)}body .appointment .search-and-notification .hamburger-rotate .hamburger-line:nth-child(2){opacity:0}body .appointment .search-and-notification .hamburger-rotate .hamburger-line:last-child{transition:.5s;-webkit-transform:rotate(45deg) translate(-8px, -10px);transform:rotate(45deg) translate(-8px, -10px)}body .appointment .search-and-notification .search{display:none}body .appointment .table-wrapper table{overflow:scroll}}@media only screen and (max-width: 1024px){.Sidebar{width:10%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.Sidebar .logo img:first-child{display:none}.Sidebar .logo img:last-child{display:block}.Sidebar p{display:none}.Sidebar li a{margin-left:0px}.Sidebar li a span{display:none}.search-and-notification .search{width:85%}.search-and-notification .notification-profile{width:20%}.cards{display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr 1fr;place-items:center}.cards .card-lists{width:95%;margin:10px 0}.cards .card-lists .info-and-number{margin-left:10px}.little-charts-container{-webkit-flex-direction:column;flex-direction:column}.little-charts-container .little-chart{width:100%}.table{overflow:scroll;display:block}}
