.search-and-notification {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  margin-bottom: 15px;
  .hamburger-menu {
    height: 50px;
    width: 50px;
    display: none;
    justify-content: center;
    align-items: center;
    z-index: 11111;
    .hamburger-line {
      width: 30px;
      margin: 3px;
      height: 2px;
      background-color: black;
      border-radius: 30px;
    }
  }
  .search {
    width: 100%;
    border-radius: 100px;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    padding: 13px 16px;

    input {
      border: none;
      padding-left: 12px;
      width: 100%;
    }
  }
  .notification-profile {
    display: flex;
    justify-content: space-around;
    width: 12%;
    .notification {
      z-index: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      cursor: pointer;
      .number {
        border: 3px solid #ffffff;
        border-radius: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        left: 18px;
        top: -3px;
        span {
          border-radius: 100%;
          background-color: #ff6760;
          width: 20px;
          height: 20px;
          display: inline-block;
          text-align: center;
          color: #ffffff;
        }
      }
    }
    .profile {
      width: 40px;
      height: 40px;
      background-image: url(../assets/circle.png);
      background-repeat: no-repeat;
      background-position: center;
      cursor: pointer;
    }
  }
}
